<template>
	<view class="all">
		<!-- 最上层注册和语言切换，后续可以抽历程公共组件 -->
		<div class="top df aic jcsb">
			<div class="df aic">
				<u-icon @click="goBack" name="arrow-leftward"></u-icon>
				<span class="fz18 fwb ml8">个人信息</span>
			</div>
			<span class="fz14 gray jcs">语言切换</span>
		</div>
		<div class="welcome fz20 mt35 md16">
			出生日期
		</div>
		<div class="fz12 nineD mb30">输入的信息与身份证/护照信息一致</div>
		<!-- 出生日期选择 -->
		<view class="sss df fdc" @click="show = true">
			<text>出生日期</text>
			<input class="ipt" type="text" v-model="isDate" placeholder="请输入真实的出生日期" />
			<u-datetime-picker ref="datetimePicker" v-model="value1" :formatter="formatter" :show="show" mode="date"
				@confirm="confirm" @cancel="close"></u-datetime-picker>
		</view>
		<!-- 继续按钮 -->
		<div class="box df aic jcc">
			<div class="btn df jcc aic mt16" @click="goAdress">继续</div>
		</div>

	</view>
</template>

<script>
	export default {
		data() {
			return {
				show: false,
				value1: Number(new Date()),
				isDate: ''
			}
		},
		methods: {
			goAdress() {
				uni.navigateTo({
					url: '/pages/userInfo/adress'
				})
			},
			formatter(type, value) {
				if (type === 'year') {
					return `${value}年`;
				}
				if (type === 'month') {
					return `${value}月`;
				}
				if (type === 'day') {
					return `${value}日`;
				}
				return value;
			},
			confirm(e) {
				this.show = false;
				const timeFormat = uni.$u.timeFormat;
				let timeValue = timeFormat(e.value, 'yyyy-mm-dd');
				this.isDate = timeValue;
				// console.log(this.isDate);
			},
			close() {
				this.show = false;
			},
			goBack() {
				uni.navigateBack(-1)
			},
		}
	}
</script>

<style scoped lang="scss">
	.u-border-bottom {
		border-bottom: 1px solid #12d18e;
	}

	.ipt {
		border-bottom: 1px solid #12d18e;
		padding: 20rpx 0;
		height: 2.4em;
	}

	.btn {
		width: 343px;
		height: 58px;
		border-radius: 100px;
		background: #12d18e;
		font-size: 16px;
		color: white;
		font-weight: bold;
	}

	.box {
		width: 100%;
		margin-top: 250px;
	}

	.sss {
		padding: 20rpx 0;
	}
</style>